<template>
	<view class="take-ticket">
		
		<view class="ticket-form-block">
			<view class="ticket-form-wrap">
				<view class="form-title">
					<text>发票抬头</text>
				</view>
				<view class="ticket-form">
					<view class="form-item">
						<view class="form-item-contemt">
							<view class="ticket-type-list">
								<view v-for="(item, index) in ticketTypeList" :key="index" :class="['type-item', { active: formData.ticketType === item.value }]" @click="toggleTypeItem(index)">
									<text>{{ item.label }}</text>
								</view>
							</view>
						</view>
					</view>
					
					<template v-if="formData.ticketType === TicketTypeEnum['企业']">
						<view class="form-item">
							<label for="">企业名称</label>
							<view class="form-item-content">
								<input type="text" v-model="formData.companyName" placeholder="请填写企业名称" placeholder-class="placeholder" />
							</view>
						</view>
						<view class="form-item">
							<label for="">纳税人识别码</label>
							<view class="form-item-content">
								<input type="text" v-model="formData.taxNumber" placeholder="请填写纳税人识别码" placeholder-class="placeholder" />
							</view>
						</view>
					</template>
					
					<template v-else>
						<view class="form-item">
							<label for="">个人名称</label>
							<view class="form-item-content">
								<input type="text" v-model="formData.taxNumber" placeholder='请填写"个人"或您的姓名' placeholder-class="placeholder" />
							</view>
						</view>
					</template>
				</view>
			</view>
			
			<view class="ticket-form-wrap">
				<view class="form-title">
					<text>收票人信息</text>
				</view>
				<view class="ticket-form">
					<view class="form-item">
						<label for="">收票人手机</label>
						<view class="form-item-content">
							<input type="text" v-model="formData.mobile" placeholder='请填写收票人手机' placeholder-class="placeholder" />
						</view>
					</view>
					<view class="form-item">
						<label for="">收票人地址</label>
						<view class="form-item-content">
							<input type="text" v-model="formData.address" placeholder='请填写收票人地址' placeholder-class="placeholder" />
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<view class="ticket-form-block">
			<view class="ticket-form-wrap">
				<view class="form-title">
					<text>发票详情</text>
				</view>
				<view class="ticket-form">
					<view class="form-item">
						<label for="">发票内容</label>
						<view class="form-item-content">
							<input type="text" v-model="formData.mobile" placeholder='请填写发票内容' placeholder-class="placeholder" />
						</view>
					</view>
					<view class="form-item">
						<label for="">发票金额</label>
						<view class="form-item-content">
							<input type="text" v-model="formData.address" placeholder='请填写发票金额' placeholder-class="placeholder" />
						</view>
					</view>
					<view class="form-item">
						<label for="">更多信息</label>
						<view class="form-item-content">
							<input type="text" v-model="formData.address" placeholder='备注(非必填)' placeholder-class="placeholder" />
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<view class="submit-button">
			<text>提交</text>
		</view>
		
	</view>
</template>

<script>
	import { TicketTypeEnum } from '@/utils/enum.js'
	import { convertEnumToList } from '@/frame/utils/enum'
	
	export default {
		data() {
			return {
				TicketTypeEnum,
				formData: {
					ticketType: TicketTypeEnum['个人']
				},
				ticketTypeList: convertEnumToList(TicketTypeEnum, 'select-list')
			}
		},
		methods: {
			toggleTypeItem(index) {
				this.formData.ticketType = this.ticketTypeList[index].value
			}
		}
	}
</script>

<style lang="scss" scoped>
	.take-ticket {
		background-color: #F0F2F5;
		min-height: 100%;
		padding: 30upx;
		box-sizing: border-box;
	}
	
	.ticket-form-block {
		background-color: #fff;
		padding: 20upx 20upx 0;
		border-radius: 16upx;
		
		& + .ticket-form-block {
			margin-top: 30upx;
		}
	}
	
	.ticket-form-wrap {
		& + .ticket-form-wrap {
			padding-top: 30upx;
			border-top: 2upx solid $border-color;
		}
	}
	
	.form-title {
		font-size: 34upx;
		font-weight: 500;
		
		& + .ticket-form {
			margin-top: 10upx;
		}
	}
	
	.ticket-form {
		font-size: 32upx;
	}
	
	.form-item {
		padding: 30upx 0;
		@include flex-c-center;
		
		label {
			flex-shrink: 0;
			min-width: 160upx;
			margin-right: 30upx;
			line-height: 44upx;
			height: 44upx;
		}
		
		.form-item-content {
			flex-grow: 1;
			width: 0;
			line-height: 44upx;
			height: 44upx;
		}
		
		& + .form-item {
			border-top: 2upx solid $border-color;
		}
	}
	
	.ticket-type-list {
		.type-item {
			display: inline-block;
			vertical-align: top;
			line-height: 56upx;
			padding: 0 50upx;
			background-color: #E4E5EF;
			border-radius: 28upx;
			color: #666666;
			transition: all .3s;
			font-size: 28upx;
			
			&.active {
				background-color: $theme-color;
				color: #fff;
			}
			
			& + .type-item {
				margin-left: 30upx;
			}
		}
	}

	.submit-button {
		margin-top: 70upx;
		background-color: $theme-color;
		color: #fff;
		line-height: 8upx;
		text-align: center;
		font-size: 32upx;
		line-height: 88upx;
		font-weight: 500;
		border-radius: 44upx;
	}
</style>
